






















import Vue from 'vue'

const colourA = 'lavender'
const colourB = 'white'

const vue = Vue.extend({
	name: 'colourSelector',
	data() {
		return {
			colours: {
				colourA,
				colourB,
			},
			width: 200,
			height: 200,
		}
	},
	watch: {
		colours: function(colours) {
			this.$emit('colours', colours)
		},
	},
})

export default vue
